import { observe } from "./observe/index";
import Watcher from "./observe/watcher";
import Dep from "./observe/dep";
export function initState(vm) {
  // Ëé∑ÂèñÊâÄÊúâÈÄâÈ°π
  const opts = vm.$options;
  if (opts.data) {
    // Êï∞ÊçÆ
    initData(vm);
  }
  if (opts.computed) {
    // ËÆ°ÁÆóÂ±ûÊÄß
    initComputed(vm);
  }
}

function initData(vm) {
  let data = vm.$options.data; // dataÂèØËÉΩÊòØÂáΩÊï∞ÊàñËÄÖÂØπË±°

  data = typeof data === "function" ? data.call(vm) : data;

  // ‰∏ãÈù¢ÁöÑÊñπÊ≥ïËøõË°å‰∫ÜÂ±ûÊÄß Âä´ÊåÅ‰ΩÜÊòØÂú®vmÂÆû‰æã‰∏äÂπ∂‰∏ç‰ºöÊúâdata Â±ûÊÄßÁõ¥Êé•ËÆøÈóÆ Â∞±ÊòØ‰∏çËÉΩÁõ¥Êé•ÂÉèÈ°πÁõÆÈáåÈù¢ this.** ËÆøÈóÆÊï∞ÊçÆÔºå ÊâÄ‰ª•È¢ùÂ§ñÂÆö‰πâ‰∏Ä‰∏™ÂèÇÊï∞
  vm._data = data; // ËøôÊ†∑Âèà‰∏ç‰æø‰∫éÁõ¥Êé•ÁöÑÊìç‰ΩúÔºå ÊâÄ‰ª•È¢ùÂ§ñËÆæÁΩÆ‰∏ÄÂ±Ç _dataÁöÑ‰ª£ÁêÜ
  // ÂØπÊï∞ÊçÆËøõË°åÂä´ÊåÅ vue2‰∏≠ÈááÁî®ÁöÑÊòØ‰∏Ä‰∏™Objec.defineProperty
  observe(data);

  // Â∞Üvm._data Áî®vmÊù•‰ª£ÁêÜ
  Object.keys(data).forEach((key) => proxy(vm, "_data", key));
}

export function proxy(vm, target, key) {
  Object.defineProperty(vm, key, {
    get() {
      return vm[target][key];
    },
    set(newVal) {
      vm[target][key] = newVal;
    },
  });
}

function initComputed(vm) {
  const computed = vm.$options.computed;
  const watcher = vm._computedWatchers = {}
  for (const key in computed) {
    if (Object.hasOwnProperty.call(computed, key)) {
      const useDef = computed[key];
      
      // Êàë‰ª¨ÈúÄË¶ÅÁõëÊéß ËÆ°ÁÆóÂ±ûÊÄß‰∏≠getÁöÑÂèòÂåñ
      const fn = typeof useDef === "function" ? useDef : useDef.get;
      // Â¶ÇÊûúÁõ¥Êé•new Watcher ÈªòËÆ§Â∞±‰ºöÊâßË°åfnÔºå ÊâÄ‰ª•Âú®options‰∏≠Â¢ûÂä†‰∏Ä‰∏™Ê†áËØÜlazy
      // Â∞ÜÂ±ûÊÄßÂíåwatcherÂØπÂ∫îËµ∑Êù•
      watcher[key] = new Watcher(vm, fn, {lazy: true})

      defineComputed(vm, key, useDef);
    }
  }
}

function defineComputed(target, key, useDef) {
  const getter = typeof useDef === "function" ? useDef : useDef.get;
  const setter = useDef.set || (() => {});
  // console.log(getter, setter);
  Object.defineProperty(target, key, {
    get: creatComputedGetter(key),
    set: setter,
  });
}

// ËÆ°ÁÆóÂ±ûÊÄßÊ†πÊú¨‰∏ç‰ºöÊî∂ÈõÜ‰æùËµñÔºåÂè™‰ºöËÆ©Ëá™Â∑±ÁöÑ‰æùËµñÂ±ûÊÄßÂéªÊî∂ÈõÜ‰æùËµñ
function creatComputedGetter(key) {
  // Êàë‰ª¨ÈúÄË¶ÅÁõëÊµãÊòØÂê¶Ë¶ÅÊâßË°åËøô‰∏™getter
  return function () {
    const watcher = this._computedWatchers[key] // Ëé∑ÂèñÂØπÂ∫îÂ±ûÊÄßÁöÑwatcher
    if (watcher.dirty) {
      // Â¶ÇÊûúÊòØËÑèÁöÑÂ∞±ÂéªÊâßË°åÁî®Êà∑‰º†ÂÖ•ÁöÑÂáΩÊï∞
      watcher.evaluate() // Áõ¥Êé•Êéâget‰∏çÂêàÈÄÇÂõ†‰∏∫Â≠òÂú®ÂÄºÂèòÂåñÈáçÊñ∞ÊâßË°åÁöÑÊÉÖÂÜµ
    }
    if (Dep.target) { // ËÆ°ÁÆóÂ±ûÊÄßÂá∫Ê†àÂêé ËøòË¶ÅÊ∏≤Êüìwatcher  ÊàëÂ∫îËØ•ËÆ©ËÆ°ÁÆóÂ±ûÊÄßwatcherÈáåÈù¢ÁöÑÂ±ûÊÄß‰πüÂéªÊî∂ÈõÜ‰∏äÂ±ÇwatcherÔºàÊ∏≤ÊüìwatcherÔºâ
        watcher.depend()      
    }
    return watcher.value // ÊúÄÂêéËøîÂõûÁöÑÊòØwatcher‰∏äÁöÑÂÄº
  }
}
