const strats = {};
  const LIFE_CYCLE = ["beforeCreate", "created"];
  LIFE_CYCLE.forEach((hook) => {
    strats[hook] = function (p, c) {
      if (c) {
        if (p) {
          return p.concat(c);
        } else {
          return [c];
        }
      } else {
        return p;
      }
    };
  });
export function mergeOptions(parent, child) {
  const options = {}
  for (const key in parent) {
    mergeField(key)
  }

  for (const key in child) {
    if (!parent.hasOwnProperty(key)) {
      mergeField(key)
    }
  }

  function mergeField(key) {
    // Á≠ñÁï•Ê®°Âºè ÈÅøÂÖçif/else  Âõ†‰∏∫mixin‰∏≠ÂèØËÉΩÂ≠òÂú®Â§ö‰∏™ÂíåÁªÑ‰ª∂ÂÜÖÂêåÁ±ªÂûãÁöÑÈîÆÂÄºÁöÑkey ÔºàcreatedÔºåwatch„ÄÇ„ÄÇ„ÄÇÔºâ
    if (strats[key]) {
      options[key] = strats[key](parent[key], child[key])
    } else {
      // ‰ºòÂÖàÈááÁî®ÂÑøÂ≠êÔºå ÂÜçÈááÁî®Áà∂‰∫≤
      options[key] = child[key] || parent[key]
    }
  }

  return options
}