import Dep, { popTarget, pushTarget } from "./dep";

// 1 ÂΩìÊàë‰ª¨ÂàõÂª∫Ê∏≤ÊüìwatcherÁöÑÊó∂ÂÄô Êàë‰ª¨‰ºöÊääÂΩìÂâçÁöÑÊ∏≤Êüìwatcher ÊîæÂà∞Dep.target ‰∏ä
// 2 Ë∞ÉÁî®_render() ‰ºöÂèñÂÄº Ëµ∞Âà∞get‰∏ä


// ÊØè‰∏™Â±ûÊÄßÈÉΩÊúâ‰∏Ä‰∏™depÔºàÂ±ûÊÄßÂ∞±ÊòØË¢´ËßÇÂØüËÄÖÔºâÔºå watcherÂ∞±ÊòØËßÇÂØüËÄÖÔºàÂ±ûÊÄßÂèòÂåñ‰∫Ü‰ºöÈÄöÁü•ËßÇÂØüËÄÖÊù•Êõ¥Êñ∞Ôºâ -„Äã ËßÇÂØüËÄÖÊ®°Âºè
let id = 0;
class Watcher {
  constructor(vm, callback, options) {
    this.id = id++
    this.vm = vm
    this.renderWatcher = options
    this.getter = callback // geter ÊÑèÂë≥ÁùÄË∞ÉÁî®Ëøô‰∏™ÂáΩÊï∞ÂèØ‰ª•Ëß¶ÂèëÂèñÂÄºÊìç‰Ωú
    this.deps = [] // ËÆ©watcherËÆ∞‰Ωèdep‰πüÊòØ‰∏∫‰∫ÜÁªÑ‰ª∂Âç∏ËΩΩÂíåËÆ°ÁÆóÂ±ûÊÄßÁöÑÂÆûÁé∞
    this.depsId = new Set()
    
    this.lazy = options.lazy
    this.dirty = this.lazy // ÁºìÂ≠òÂÄº
    this.lazy ? undefined : this.get()
  }
  // Âà§Êñ≠dirtyÈáçÊñ∞ÊâßË°å
  evaluate() {
    this.value = this.get() // Ëé∑ÂèñÁî®Êà∑ÂáΩÊï∞ÁöÑËøîÂõûÂÄºÔºåÂπ∂‰∏îËøòË¶ÅÊ†áËÆ∞‰∏∫ËÑè
    this.dirty = false
  }
  get() {
    // Dep.target = this // ÈùôÊÄÅÂ±ûÊÄßÂ∞±ÊòØÂè™Êúâ‰∏Ä‰ªΩ
    pushTarget(this)
    let value = this.getter.call(this.vm) // ‰ºöÂéªvm‰∏äÂèñÂÄº
    // Dep.target = null // Ê∏≤ÊüìÂÆåÊØïÂ∞±Ê∏ÖÁ©∫ÔºàÊ∏ÖÁ©∫ÊòØ‰∏∫‰∫Ü‰øùËØÅÂè™ÊúâÂú®Ê®°ÊùøÈáåÈù¢ÊâçÊî∂ÈõÜÔºåÂú®vm‰∏äËé∑ÂèñÂ±ûÊÄßÊòØ‰∏çÊî∂ÈõÜÁöÑÔºâ
    popTarget(this)
    return value
  }
  depend() {
    let i = this.deps.length
    while (i--) {
      this.deps[i].depend() // ËÆ©ËÆ°ÁÆóÂ±ûÊÄßwatcher ‰πüÊî∂ÈõÜÊ∏≤Êüìwatcher
    }
  }

  addDep(dep) {
    let id = dep.id
    if(!this.depsId.has(id)) {
      this.deps.push(dep)
      this.depsId.add(id)
      dep.addSub(this); // watcherËÆ∞‰Ωè‰∫Üdep‰∫ÜËÄå‰∏îÂéªÈáç‰∫Ü, Ê≠§Êó∂dep ‰πüËÆ∞‰Ωèwatcher‰∫Ü
    }
  }

  update() {
    if (this.lazy) {
      // Â¶ÇÊûúÊòØËÆ°ÁÆóÂ±ûÊÄß ‰æùËµñÁöÑÂÄºÂèòÂåñ‰∫Ü Â∞±Ê†áËÆ∞ËÆ°ÁÆóÂ±ûÊÄßÊòØËÑèÂÄº‰∫Ü
      this.dirty = true  
    } else {
      // this.get() // ÈáçÊñ∞Ê∏≤ÊüìÊõ¥Êñ∞ Ôºà‰∏çËÉΩÁõ¥Êé•ÂêåÊ≠•Êõ¥Êñ∞ÔºåÂ§öÊ¨°setÂÄº‰ºöÈáçÂ§çÊ∏≤ÊüìÔºâ
      queueWatcher(this)
    }
  }

  run() {
    console.log('update');
    this.get()
  }
}

let queue = []
let has = {}
let pending = false; // Èò≤Êäñ

function flushSchedulerQueue() {
  let fulshQueue = queue.slice(0)
  queue = []
  has = {}
  pending = false
  fulshQueue.forEach(q => 
    q.run()
  )
}

function queueWatcher(watcher) {
  const id = watcher.id
  if (!has[id]) {
    queue.push(watcher)
    has[id] = true
    // ‰∏çÁÆ°update ÊâßË°åÂ§öÂ∞ëÊ¨°ÔºåÂè™ÊâßË°å‰∏ÄËΩÆÂà∑Êñ∞Êìç‰Ωú

    if (!pending) {
      // setTimeout(flushSchedulerQueue, 0) // ‰∏çËÉΩÁõ¥Êé•ÂÜôÂºÇÊ≠•ÁöÑÂÆè‰ªªÂä°ÔºåÂõ†‰∏∫ÂºÇÊ≠•Êõ¥Êñ∞ÁöÑÊó∂ÂÄôËé∑Âèñ‰∏çÂà∞ÁúüÂÆûdomÊï∞ÊçÆ
      nextTick(flushSchedulerQueue, 0)
      pending = true
    }
  }
}

// nextTickÊ≤°ÊúâÁõ¥Êé•‰ΩøÁî®Êüê‰∏™api  ËÄåÊòØÈááÁî®‰ºòÈõÖÈôçÁ∫ßÁöÑÊñπÂºè
// ÂÜÖÈÉ®ÂÖàÈááÁî®ÁöÑÊòØPromise Ôºàie‰∏çÂÖºÂÆπÔºâÔºå Âú®ÁúãMutationObserver Ôºå 
// Ëøò‰∏çÊîØÊåÅÂèØ‰ª•ËÄÉËôë ie‰∏ìÂ±ûÁöÑ setImmediate ÊúÄÂêé setTimeOut
let callbacks = []
let waiting = false
export function nextTick(cb) { // ÂÖàÊâßË°åÂÜÖÈÉ®ËøòÊòØÂÖàÁî®Êà∑ÁöÑÔºü
  callbacks.push(cb) // Áª¥Êä§nextTick‰∏≠ÁöÑcallbackÊñπÊ≥ï
  if (!waiting) {
    // setTimeout(() => {
    //   flushCallBacks() // ÊúÄÂêé‰∏ÄËµ∑Âà∑Êñ∞
    // }, 0)
    timerFunc()
    waiting = true
  }
}
let timerFunc;
if (Promise) {
  timerFunc = () => {
    Promise.resolve().then(flushCallBacks)
  }
} else if (MutationObserver) {
  let observer = new MutationObserver(flushCallBacks)
  let textNode = document.createTextNode(1);
  observer.observe(textNode, {
    characterData: true
  })
  timerFunc = () => {
    textNode.textContent = 2
  }
} else if (setImmediate) {
  timerFunc = () => {
    setImmediate(flushCallBacks)
  }
} else {
  timerFunc = () => {
    setTimeout(flushCallBacks)
  }
}
function flushCallBacks() {
  let cbs = callbacks.slice(0)
  callbacks = []
  waiting = false
  cbs.forEach(cb => cb())
}

// ÈúÄË¶ÅÁªôÊØè‰∏™Â±ûÊÄßÂ¢ûÂä†‰∏Ä‰∏™dep  ÁõÆÁöÑÂ∞±ÊòØÊî∂ÈõÜwatcher
//  ‰∏Ä‰∏™ÁªÑ‰ª∂‰∏≠ ÊúâÂ§ö‰∏™Â±ûÊÄß n‰∏™Â±ûÊÄßÂØπÂ∫î‰∏Ä‰∏™ËßÜÂõæ  n‰∏™depÂØπÂ∫î‰∏Ä‰∏™watcher
// 1‰∏™Â±ûÊÄßÂØπÂ∫îÂ§ö‰∏™ÁªÑ‰ª∂(Âú®Â§ö‰∏™ÁªÑ‰ª∂ÂºïÁî®)  1‰∏™depÂØπÂ∫îÂ§ö‰∏™watcher
// Â§öÂØπÂ§öÁöÑÂÖ≥Á≥ª

export default Watcher
